@use 'sass:math';


.card{
    display: block;
    padding: $base-padding;
    box-shadow:$base-box-shadow;
    max-width: 400px;
    border-radius: math.div($base-border-radius, 4);

    &__title{
        font-size: $font-size-xl;
        padding-bottom: $base-padding;
        font-weight: bold;

    }
    &__body{
        
        &__paragraph{
            font-size: $base-font-size;
        }
        &__link{
            display: inline-block;
            border:$base-border-thickness solid $clr-gray;
            padding:$base-padding;
            font-size: $base-font-size;
            margin:$base-margin;

            &:hover{
              background:$clr-black;
              color:$clr-white;
            }
        }
    }
}


@debug "hello coder";
@debug math.div(10,3);
@debug math.div(23,3);
@debug math.floor(2.6);
@debug math.max(1px , 20px, 14px ,19px);